.page-view{padding:60px 0;color:#313131;-webkit-box-sizing:border-box;box-sizing:border-box}.page-view .content{max-width:700px;width:90%;margin:0 auto}.page-view .content .input-wrapper .input-box{position:relative}.page-view .content .input-wrapper .input-box .input-inner{font-size:14px;line-height:40px;width:100%;height:40px;padding:0 40px 0 18px;color:#313131;border:1px solid rgba(184,197,214,0.2);border-radius:20px;outline:0;background-color:#fff;-webkit-box-shadow:0 1px 4px rgba(0,0,0,0.04);box-shadow:0 1px 4px rgba(0,0,0,0.04);-webkit-box-sizing:border-box;box-sizing:border-box}.page-view .content .input-wrapper .input-box .search-btn{position:absolute;right:10px;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);padding:5px;cursor:pointer}.page-view .content .hot-search-tags{padding:15px 0}.page-view .content .hot-search-tags .help-guide{margin-top:20px;font-size:14px;text-align:center}.page-view .content .hot-search-tags .tag-list{display:-webkit-box;display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;margin-top:10px}.page-view .content .hot-search-tags .tag-list .tag{font-size:13px;display:inline-block;margin:10px 8px 0 0;padding:2px 15px;-webkit-transition-duration:.4s;transition-duration:.4s;letter-spacing:0;border-radius:15px;-webkit-box-shadow:0 1px 4px rgba(0,0,0,0.04);box-shadow:0 1px 4px rgba(0,0,0,0.04);border:1px solid rgba(184,197,214,0.2);background:#fff;line-height:1.625}.page-view .content .hot-search-tags .tag-list .tag:hover{color:#3274ff}
